.actionBtns {
  display: flex;
  align-items: center;
  .startBtn {
    padding: 12px 16px;
    width: 88px;
    font-weight: 400;
  }
  .iconBtn {
    font-size: 18px;
    padding: 12px 16px;
    width: 40px;
    &:last-child {
      margin-left: 8px;
    }
  }
  .stopIcon {
    width: 12px;
    height: 12px;
    min-width: 12px;
    background-color: currentColor;
    border-radius: 2px;
  }
}
