.container {
  height: 100%;
  width: 100%;
  background-color: #201f1e;
  color: #e3e3e3;
  position: relative;
  .panel {
    display: flex;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .switchTheme {
      margin-top: 12px;
      margin-bottom: 20px;
    }
    .time {
      width: 360px;
      height: 360px;
      font-family: Arial;
      font-size: 70px;
      font-weight: 700;
      align-items: center;
      justify-content: center;
      display: flex;
      position: relative;
    }
    .actionBtns {
      margin-top: 16px;
      margin-bottom: 16px;
    }
    .settingBtns {
      margin-top: 16px;
      margin-bottom: 16px;
    }
  }
  .copyright {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(42, 56, 27, 0.6), rgba(18, 18, 16, 0.7));
    }
  }
}
