.copyright {
  font-size: 12px;
  // color: var(--system-text-color, rgb(44 52 55 / 1));
  color: #e3e3e3;
  opacity: 0.45;
  line-height: 16px;
  text-align: center;
  padding: 8px 0;
  user-select: none;
  & > div {
    display: none;
    &:nth-child(1),
    &:nth-child(2) {
      display: block;
    }
  }
  &:hover {
    & > div {
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        display: block;
      }
    }
  }
}
