.container {
  width: 100%;
  height: 100%;
  font-size: 14px;
  position: relative;
  color: #fff;

  .panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 10px;
  }

  .bgCover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .mask, .bgImage {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .mask {
      background: linear-gradient(rgba(31, 31, 29, 0.6), rgba(0, 0, 0, 0.7));
    }

    .bgImage {
      background-size: cover;
    }
  }

  .left {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
  }

  .right {
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 10px;
  }

  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.3);
  }

  .title {
    font-weight: 600;
    font-size: 28px;
  }

  .info {
    font-size: 12px;
  }
}
