.switchTheme {
  color: #e3e3e3;
  font-size: 16px;
  & > li {
    background-color: transparent !important;
    border: none !important;
    & > button {
      background-color: transparent !important;
      color: #e3e3e3 !important;
    }
  }
  :global {
    .ant-pagination-item-active {
      margin: 0 16px !important;
    }
  }
}
